.qhPagination {
  margin-top: 10px;
}
.qhPagination .ant-pagination-prev .ant-pagination-item-link,
.qhPagination .ant-pagination-next .ant-pagination-item-link,
.qhPagination .ant-pagination-item {
  background-color: white !important;
  border-color: #d9d9d9;
}
.qhPagination .ant-pagination-prev .ant-pagination-item-link a:hover,
.qhPagination .ant-pagination-next .ant-pagination-item-link a:hover,
.qhPagination .ant-pagination-item a:hover {
  background: unset;
}
.qhPagination .ant-pagination-item-active {
  background: #1890ff;
  border-color: #1890ff;
}
